import * as Swarms from "@hervan/swarms";

type Species = Swarms.Player.Species.Species;

const { Species } = Swarms.Player.Species;

export const emojiFromSpecies = (species: Species) => {
  switch (species) {
    case Species.Fish:
      return "";
    case Species.Bird:
      return "";
    case Species.Scarab:
      return "";
    case Species.Fungi:
      return "";
    default:
      return "";
  }
};
