import { deepAssign, partition, unique } from "@hervan/utils";
import { Blueprint } from "Blueprint";
import { SwarmsState } from "index";
import { blueprintMastery, upgradeGrid } from "..";
import { NUM_PLAYERS } from "../../..";
import { Action, ActionList } from "../../../Action";
import {
  Coordinates,
  TerrainMap,
  applyMapEffect,
  clusterCity,
  getAdjacentCoordinates,
  getAdjacentToSelection,
  sameCoordinates,
} from "../../../Map";
import {
  TerrainList,
  TerrainType,
  relativeResourceValues,
  resourceValues,
  terrainFromMap,
} from "../../../Map/Terrain";
import { Player, sortResources } from "../../../Player";

export type Slot<T extends Action> = {
  action: T;
  row: number;
  blueprint?: Blueprint;
};

type ActionRunner = (G: SwarmsState, id: number) => SwarmsState;

const spread: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const destination = currentPlayer.selection.destination;
  const map = applyMapEffect(G.map, destination, {
    state: "occupied",
    owner: id,
  });
  const resources = currentPlayer.resources.slice(4);
  const swarm = [...currentPlayer.swarm, ...destination];
  const player = { ...currentPlayer, swarm, resources };
  return deepAssign({ ...G, map }, player, "players", (p) => p.id === id);
};

const build: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const slot = currentPlayer.selection.slot;
  if (slot === undefined) {
    return G;
  }
  const token = slot.blueprint;
  if (token === undefined) {
    return G;
  }
  const destination = currentPlayer.selection.destination;
  const map = applyMapEffect(G.map, destination, {
    state: "built",
    blueprint: token,
    owner: id,
  });
  const city = clusterCity([...currentPlayer.city, destination]);
  const swarm = currentPlayer.swarm.filter(
    (c1) => !destination.some((c2: Coordinates) => sameCoordinates(c1, c2))
  );
  const resources = currentPlayer.resources.slice(4);
  const player = { ...currentPlayer, city, swarm, resources };
  const players = G.players.map((p) => (p.id === id ? player : p));
  const bag = [...G.market.bag, token];
  const market = { ...G.market, bag };
  return { ...G, map, players, market };
};

const trade: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const slot = currentPlayer.selection.slot;
  if (slot === undefined) {
    return G;
  }
  const token = slot.blueprint;
  if (token === undefined) {
    return G;
  }

  const movedMap = applyMapEffect(G.map, currentPlayer.selection.location, {
    state: "free",
  });
  const map = applyMapEffect(movedMap, currentPlayer.selection.destination, {
    state: "occupied",
    owner: id,
  });
  const swarm = [
    ...currentPlayer.swarm.filter((c1) =>
      currentPlayer.selection.location.every((c2) => !sameCoordinates(c1, c2))
    ),
    ...currentPlayer.selection.destination,
  ];

  const targetOpponents = [
    ...getAdjacentToSelection(currentPlayer.selection.destination)
      .map((c) => terrainFromMap(G.map, c))
      .map((t) => (t.state === "occupied" ? t.owner : id))
      .filter((playerId) => playerId !== id),
  ].sort();
  const targetDeliveries = targetOpponents
    .filter(unique)
    .map((targetId) => G.players[targetId])
    .map((player) => {
      const demand = Math.max(
        0,
        targetOpponents.findLastIndex((i) => i === player.id) -
          targetOpponents.findIndex((i) => i === player.id) +
          1
      );
      const mastery = blueprintMastery(currentPlayer.grid, token);
      const possibleSales = (resource: TerrainType) =>
        Math.min(
          demand * mastery,
          currentPlayer.resources.filter((r) => r === resource).length
        );
      const demandedResource = [...TerrainList]
        .sort((a, b) => possibleSales(b) - possibleSales(a))
        .filter(
          (r) =>
            possibleSales(r) > 0 &&
            resourceValues(player.id, r) - resourceValues(id, r) > 0
        )[0];
      return {
        ...player,
        sale:
          demandedResource === undefined
            ? []
            : Array(possibleSales(demandedResource)).fill(demandedResource),
        demand,
      };
    }) as (Player & {
    sale: TerrainType[];
    demand: number;
  })[];
  const sellerValues = [...TerrainList].sort(
    (a, b) => resourceValues(id, a) - resourceValues(id, b)
  );
  const resources = G.players.reduce(
    ({ cr, rs, totalDemand }, p) => {
      if (p.id === id) {
        return { cr, rs, totalDemand };
      }
      const target = targetDeliveries.find((td) => td.id === p.id);
      if (target === undefined || target.sale.length === 0) {
        return { cr, rs, totalDemand };
      }
      const product = target.sale[0];
      const buyerValues = [...TerrainList].sort(
        (a, b) => resourceValues(p.id, a) - resourceValues(p.id, b)
      );
      const payment =
        resourceValues(p.id, product) === relativeResourceValues[2]
          ? sellerValues[sellerValues.length - 1]
          : buyerValues[0];
      const [tS, tK] = partition(cr, (r) => r === product);
      const sold = tS.slice(0, target.sale.length);
      const kept = [...tK, ...tS.slice(target.sale.length)];
      return {
        cr: [...sold.map((_) => payment), ...kept],
        rs: rs.map((buyer, i) =>
          i === p.id ? [...buyer.slice(sold.length), ...sold] : buyer
        ),
        totalDemand: totalDemand + target.demand,
      };
    },
    {
      cr: currentPlayer.resources,
      rs: G.players.map((p) => p.resources),
      totalDemand: 0,
    }
  );
  const delivery = currentPlayer.delivery + resources.totalDemand;
  return {
    ...G,
    map,
    players: G.players
      .map((p, i) =>
        i === id
          ? { ...currentPlayer, swarm, delivery, resources: resources.cr }
          : { ...p, resources: resources.rs[i] }
      )
      .map((p) => sortResources(G, p)),
  };
};

export const scienceScore = (player: Player, map: TerrainMap) => [
  Math.trunc(
    player.swarm.filter((critter) =>
      getAdjacentCoordinates(critter)
        .map((adj) => terrainFromMap(map, adj))
        .some((adj) => adj.state === "occupied" && adj.owner !== player.id)
    ).length / 4
  ),
  Math.max(0, ...player.city.map((cluster) => Math.trunc(cluster.length / 4))),
  player.delivery,
  player.grid.filter((row) =>
    ActionList.every((a) => row[a].blueprint !== undefined)
  ).length +
    ActionList.filter((a) =>
      player.grid.every((row) => row[a].blueprint !== undefined)
    ).length,
];

const research: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const slot = currentPlayer.selection.slot;
  if (slot === undefined) {
    return G;
  }
  const token = slot.blueprint;
  if (token === undefined) {
    return G;
  }
  const players = G.players.map((p) => ({
    ...p,
    grid: upgradeGrid(
      p,
      p.grid[slot.row][Action.Research],
      G.market.stalls.filter((t) => t === token)
    ),
  }));
  const [usedTokens, leftTokens] = partition(
    G.market.stalls,
    (bp) => bp === token
  );

  return {
    ...G,
    market: {
      ...G.market,
      stalls: [...leftTokens, ...usedTokens.slice(NUM_PLAYERS - 1)],
    },
    players,
  };
};

export const getActionRunner: (action: Action) => ActionRunner = (action) => {
  switch (action) {
    case Action.Spread:
      return spread;
    case Action.Build:
      return build;
    case Action.Trade:
      return trade;
    case Action.Research:
      return research;
  }
};
