import { Blueprint } from "../../Blueprint";
import { Coordinates, TerrainMap } from "../../Map";

export enum TerrainType {
  Water = "water",
  Desert = "desert",
  Forest = "forest",
  Mountain = "mountain",
}

export const TerrainList: TerrainType[] = Object.values(TerrainType);

export const playerTerrainOrder = (id: number) => [
  ...TerrainList.slice(id),
  ...TerrainList.slice(0, id),
];

export const relativeResourceValues = [0, 1, 2, 1];

export const resourceValues = (id: number, resource: TerrainType) =>
  relativeResourceValues[
    playerTerrainOrder(id).findIndex((t) => t === resource)
  ] || 0;

type FreeTerrain = {
  state: "free";
};

type OccupiedTerrain = {
  state: "occupied";
  owner: number;
};

type BuiltTerrain = {
  state: "built";
  blueprint: Blueprint;
  owner: number;
};

type TerrainState = FreeTerrain | OccupiedTerrain | BuiltTerrain;

export type Terrain = {
  type: TerrainType;
  coord: Coordinates;
} & TerrainState;

export const terrainFromMap: (
  map: TerrainMap,
  { row, col }: Coordinates
) => Terrain = (map, { row, col }) => map[row][col];

type BoundingBox = {
  topLeft: Coordinates;
  bottomRight: Coordinates;
};

export const getBoundingBox: (swarm: Coordinates[]) => BoundingBox = (
  swarm
) => ({
  topLeft: {
    row: Math.min(...swarm.map((o) => o.row)),
    col: Math.min(...swarm.map((o) => o.col)),
  },
  bottomRight: {
    row: Math.max(...swarm.map((o) => o.row)),
    col: Math.max(...swarm.map((o) => o.col)),
  },
});

export const terrainToIndex = (terrain: TerrainType) =>
  TerrainList.findIndex((t) => t === terrain);
