export const GAME_SIZE = 20;
const FEATURE_SIZE = 4;
const CORNER_SIZE = 4;

export type Coordinates = { row: number; col: number };

export type TerrainMap = Terrain[][];

export const isWithinMap = ({ row, col }: Coordinates) =>
  row >= 0 && row <= GAME_SIZE - 1 && col >= 0 && col <= GAME_SIZE - 1;

export const sameCoordinates = (c1: Coordinates, c2: Coordinates) =>
  c1.row === c2.row && c1.col === c2.col;

export const containsCoord = (coords: Coordinates[], coord: Coordinates) =>
  coords.some((c) => sameCoordinates(c, coord));

export const coordinatesOverlap = (
  coords1: Coordinates[],
  coords2: Coordinates[]
) => coords1.some((c1) => coords2.some((c2) => sameCoordinates(c1, c2)));

export const containsCoords = (
  coords1: Coordinates[],
  coords2: Coordinates[]
) => coords2.every((c) => containsCoord(coords1, c));

export const getAdjacentCoordinates: (coord: Coordinates) => Coordinates[] = ({
  row,
  col,
}) =>
  [
    { row: row - 1, col },
    { row: row + 1, col },
    { row, col: col - 1 },
    { row, col: col + 1 },
  ].filter(isWithinMap);

export const isAdjacentTo: (c1: Coordinates, c2: Coordinates) => boolean = (
  c1,
  c2
) => getAdjacentCoordinates(c1).some((c) => sameCoordinates(c, c2));

export const getAdjacentToSelection: (
  coords: Coordinates[]
) => Coordinates[] = (coords) =>
  coords.reduce(
    (acc: Coordinates[], coord: Coordinates) => [
      ...acc,
      ...getAdjacentCoordinates(coord).filter(
        (c1) => ![...acc, ...coords].some((c2) => sameCoordinates(c1, c2))
      ),
    ],
    []
  );

const getCoordinateNeighbours: (coord: Coordinates) => Coordinates[] = ({
  row,
  col,
}) =>
  [
    { row: row - 1, col },
    { row: row + 1, col },
    { row, col: col - 1 },
    { row, col: col + 1 },
    { row: row - 1, col: col - 1 },
    { row: row - 1, col: col + 1 },
    { row: row + 1, col: col - 1 },
    { row: row + 1, col: col + 1 },
  ].filter(isWithinMap);

const getSelectionNeighbours: (coords: Coordinates[]) => Coordinates[] = (
  coords
) =>
  coords.reduce(
    (acc: Coordinates[], coord: Coordinates) => [
      ...acc,
      ...getCoordinateNeighbours(coord).filter(
        (c1) => ![...acc, ...coords].some((c2) => sameCoordinates(c1, c2))
      ),
    ],
    []
  );

export const clusterCity = (city: Coordinates[][]) =>
  city.reduce((passCity, passCluster) => {
    const [newCluster, oldClusters] = partition(passCity, (c) =>
      coordinatesOverlap(getSelectionNeighbours(passCluster), c)
    );
    return [
      newCluster.length === 0
        ? passCluster
        : [passCluster, ...newCluster].flat(),
      ...oldClusters,
    ];
  }, [] as Coordinates[][]);

export const clusterSwarm = (swarm: Coordinates[]) =>
  swarm.reduce((passSwarm: Coordinates[][], passCoordinate: Coordinates) => {
    const [adjacentClusters, oldClusters] = partition(passSwarm, (c) =>
      coordinatesOverlap(getAdjacentToSelection([passCoordinate]), c)
    );
    return [
      adjacentClusters.length === 0
        ? [passCoordinate]
        : [...adjacentClusters.flat(), passCoordinate],
      ...oldClusters,
    ];
  }, [] as Coordinates[][]);

export const applyMapEffect: (
  map: TerrainMap,
  coords: Coordinates[],
  terrainProps: Partial<Terrain>
) => TerrainMap = (map, coords, terrainProps) =>
  map.map((row, i) =>
    row.map((cell, j) =>
      coords.some(({ row, col }) => row === i && col === j)
        ? ({ ...cell, ...terrainProps } as Terrain)
        : cell
    )
  );

