import type * as Swarms from "@hervan/swarms";
import { numberlist } from "@hervan/utils";
import { emojiFromSpecies } from "Board/Species";
import { TIME_EMOJI } from "../theme";

interface TrackProps {
  players: Swarms.Player.Player[];
  track: Swarms.Track.Track;
}

export const TurnTrack = ({ players, track }: TrackProps) => {
  const times = players.map((p) => p.timeSpent);
  const minTime = Math.max(0, Math.trunc(Math.min(...times) / 5) * 5);
  const maxTime = (Math.trunc(Math.max(...times) / 5) + 1) * 5;
  const trackLength = maxTime - minTime;
  return (
    <table>
      <tbody>
        <tr>
          <td
            colSpan={2}
            style={{ verticalAlign: "middle", textAlign: "right" }}
          >
            {TIME_EMOJI}
          </td>
          <td style={{ verticalAlign: "middle" }}>
            {numberlist(trackLength).map((t) => {
              const time = t + minTime;
              const nowPlayers = track[time] || [];
              if (nowPlayers.length > 0) {
                return `[${nowPlayers
                  .map((p) => players[p])
                  .map(({ species }) => emojiFromSpecies(species))
                  .join("")}]`;
              }
              return time % 10 === 0
                ? `[${time}]`
                : time % 5 === 0
                ? "[]"
                : "[ ]";
            })}
            [{maxTime}]
          </td>
        </tr>
      </tbody>
    </table>
  );
};
