import "index.css";
import React from "react";
import ReactDOM from "react-dom/client";
import reportWebVitals from "reportWebVitals";
import App from "./App";

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);

const [readMatchID, readPlayerID] = [
  ...window.location.pathname.matchAll(/\/(\d+)/g),
].map((p) => p[1]);
const matchID: string =
  parseInt(readMatchID || "") > 0
    ? (readMatchID as string)
    : Math.trunc(Math.random() * 1000000000000).toString();
const playerID = readPlayerID || "0";

console.log({ matchID, playerID });

const AppHOC = App(matchID);

root.render(
  <React.StrictMode>
    <AppHOC matchID={matchID} playerID={playerID} />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
