import { SwarmsState } from "index";
import { Action, Selection, noSelection } from "../Action";
import { Blueprint, BlueprintList } from "../Blueprint";
import { Coordinates } from "../Map";
import { TerrainType, resourceValues } from "../Map/Terrain";
import { Track } from "../Track";
import { TechGrid, blueprintMastery, playerStartingGrid } from "./Grid";
import { scienceScore } from "./Grid/Slot";
import { Species, SpeciesList } from "./Species";

export type Player = {
  id: number;
  species: Species;
  blueprint: Blueprint;
  corner: Coordinates;
  timeSpent: number;
  swarm: Coordinates[];
  city: Coordinates[][];
  selection: Selection<Action>;
  grid: TechGrid;
  delivery: number;
  resources: TerrainType[];
  score: number;
};

export const newPlayer: (id: number, corner: Coordinates) => Player = (
  id,
  corner
) => ({
  id,
  species: SpeciesList[id],
  blueprint: BlueprintList[BlueprintList.length - id - 1],
  corner,
  timeSpent: 0,
  swarm: [],
  city: [],
  selection: noSelection,
  grid: playerStartingGrid(),
  delivery: 0,
  resources: [],
  score: 0,
});

export const resourceScore = (player: Player) =>
  player.resources.reduce((acc, r) => acc + resourceValues(player.id, r), 0);

export const playerScore = (G: SwarmsState, player: Player) =>
  scienceScore(player, G.map).reduce((acc, score, i) => {
    const bpm = blueprintMastery(
      player.grid,
      player.grid[i][Action.Research].blueprint
    );
    const res = score * bpm;
    return acc + res;
  }, resourceScore(player));

export const sortResources = (G: SwarmsState, player: Player) => ({
  ...player,
  resources: [...player.resources].sort((a, b) => {
    const va = resourceValues(player.id, a);
    const vb = resourceValues(player.id, b);
    if (va === vb) {
      return a < b ? -1 : 1;
    } else {
      return va - vb;
    }
  }),
  score: playerScore(G, player),
});

const tiebreaker = (p1: Player, p2: Player) =>
  p1.score > p2.score
    ? p1
    : p1.score < p2.score
    ? p2
    : p1.timeSpent < p2.timeSpent
    ? p1
    : p2;

export const winner = (players: Player[]) => players.reduce(tiebreaker);

export const nextPlayer = (
  players: Player[],
  turn: Track,
  gameEndTriggered?: number
) => nextPlayers(players, turn, gameEndTriggered)[0];

export const nextPlayers = (
  players: Player[],
  turn: Track,
  gameEndTriggered?: number
) =>
  turn[
    players.reduce(
      (acc, { timeSpent }) => Math.min(timeSpent, acc),
      gameEndTriggered || players[0].timeSpent
    )
  ];

export * as Grid from "./Grid";
export * as Species from "./Species";
