import * as Swarms from "@hervan/swarms";
import { numberlist } from "@hervan/utils";

type Coordinates = Swarms.Map.Coordinates;
type Blueprint = Swarms.Blueprint.Blueprint;

const { Blueprint, BlueprintList } = Swarms.Blueprint;
const { addCoordinates, scaleCoordinates, subtractCoordinates } = Swarms.Map;
const { NUM_PLAYERS } = Swarms;

const TOKEN_SIZE = 9.5;
const PIP_SIZE = TOKEN_SIZE / 5;

export interface BlueprintProps {
  blueprint?: Blueprint;
  selected: boolean;
}

interface PipProps {
  coord: Coordinates;
  color: string;
  selected?: boolean;
}

export const Pip = ({
  coord: { row, col },
  color,
  selected = true,
}: PipProps) => (
  <rect
    x={col}
    y={row}
    width={PIP_SIZE}
    height={PIP_SIZE}
    rx={PIP_SIZE / 5}
    fill={color}
    stroke="black"
    strokeDasharray={selected ? "0" : "0.2"}
    strokeWidth={PIP_SIZE / 50}
  />
);

export const blueprintPips: (blueprint?: Blueprint) => Coordinates[] = (
  blueprint
) =>
  blueprint
    ? {
        [Blueprint.O]: [
          { row: 0, col: 0 },
          { row: 0, col: 1 },
          { row: 1, col: 0 },
          { row: 1, col: 1 },
        ],
        [Blueprint.I]: [
          { row: 0, col: 0 },
          { row: 0, col: 1 },
          { row: 0, col: 2 },
          { row: 0, col: 3 },
        ],
        [Blueprint.T]: [
          { row: 0, col: 0 },
          { row: 0, col: 1 },
          { row: 0, col: 2 },
          { row: 1, col: 1 },
        ],
        [Blueprint.S]: [
          { row: 0, col: 1 },
          { row: 0, col: 2 },
          { row: 1, col: 0 },
          { row: 1, col: 1 },
        ],
        [Blueprint.L]: [
          { row: 0, col: 0 },
          { row: 0, col: 1 },
          { row: 0, col: 2 },
          { row: 1, col: 0 },
        ],
        [Blueprint.Z]: [
          { row: 0, col: 0 },
          { row: 0, col: 1 },
          { row: 1, col: 1 },
          { row: 1, col: 2 },
        ],
        [Blueprint.J]: [
          { row: 0, col: 0 },
          { row: 0, col: 1 },
          { row: 0, col: 2 },
          { row: 1, col: 2 },
        ],
      }[blueprint]
    : [
        { row: 0, col: 0 },
        { row: 0, col: 1 },
        { row: 0, col: 2 },
        { row: 0, col: 3 },
        { row: 1, col: 0 },
        { row: 1, col: 1 },
      ];

const blueprintSizes: (blueprint?: Blueprint) => Coordinates = (blueprint) => {
  const pips = blueprintPips(blueprint);
  const pipRows = pips.map(({ row }) => row);
  const pipCols = pips.map(({ col }) => col);
  const rowMin = Math.min(...pipRows);
  const rowMax = Math.max(...pipRows);
  const colMin = Math.min(...pipCols);
  const colMax = Math.max(...pipCols);
  const row = rowMax - rowMin + 1;
  const col = colMax - colMin + 1;
  return { row, col };
};

export interface BlueprintPipsProps {
  blueprint?: Blueprint;
  coord: Coordinates;
  size: number;
  selected?: boolean;
}

export const blueprintPipsCoords: (
  blueprint?: Blueprint
) => { pip: Coordinates; coord: Coordinates }[] = (blueprint) =>
  blueprintPips(blueprint).map((pip) => ({
    pip,
    coord: scaleCoordinates(
      subtractCoordinates(
        scaleCoordinates(blueprintSizes(blueprint), 0.5),
        pip
      ),
      PIP_SIZE
    ),
  }));

const BlueprintTokenPips = ({
  blueprint,
  coord,
  size,
  selected,
}: BlueprintPipsProps) => (
  <>
    {blueprintPipsCoords(blueprint).map((pipCoord, i) => (
      <Pip
        key={i}
        coord={addCoordinates(
          addCoordinates({ row: size / 2, col: size / 2 }, coord),
          pipCoord.coord
        )}
        color={selected ? "white" : blueprintColor(blueprint)}
      />
    ))}
  </>
);

export const BlueprintToken = ({
  coord,
  blueprint,
  selected,
}: BaseBlueprintProps) => (
  <>
    <circle
      cx={coord.col + TOKEN_SIZE / 2}
      cy={coord.row + TOKEN_SIZE / 2}
      r={TOKEN_SIZE / 2}
      fill={selected ? blueprintColor(blueprint) : "white"}
      stroke="black"
      strokeWidth={CONTROL_LINE}
    />
    <BlueprintTokenPips
      blueprint={blueprint}
      coord={coord}
      size={TOKEN_SIZE}
      selected={selected}
    />
  </>
);

export const blueprintColor = (blueprint?: Blueprint) =>
  blueprint
    ? {
        [Blueprint.O]: "yellow",
        [Blueprint.I]: "cyan",
        [Blueprint.T]: "purple",
        [Blueprint.S]: "green",
        [Blueprint.L]: "orange",
        [Blueprint.Z]: "red",
        [Blueprint.J]: "blue",
      }[blueprint]
    : "white";

const PADDING = 0.5;
const CONTROL_LINE = 0.02;

interface BaseBlueprintProps {
  coord: Coordinates;
  blueprint?: Blueprint;
  selected?: boolean;
}

const numToBlueprint: (num: number) => Blueprint = (num) =>
  [
    Blueprint.O,
    Blueprint.I,
    Blueprint.T,
    Blueprint.S,
    Blueprint.L,
    Blueprint.J,
    Blueprint.Z,
    Blueprint.T,
    Blueprint.I,
    Blueprint.O,
  ][num];

export const TokensPrintSheet = () => (
  <svg
    height="200vmin"
    width="125vmin"
    preserveAspectRatio="xMinYMin slice"
    viewBox="0 0 100 200"
    style={{ overflow: "visible" }}
  >
    <line
      x1={(TOKEN_SIZE + PADDING) * BlueprintList.length - PADDING / 2}
      y1={0}
      x2={(TOKEN_SIZE + PADDING) * BlueprintList.length - PADDING / 2}
      y2="200"
      stroke="black"
      strokeWidth={CONTROL_LINE}
    />
    {numberlist(Math.pow(NUM_PLAYERS, 2)).map((i) =>
      numberlist(BlueprintList.length * 2)
        .map(numToBlueprint)
        .map((blueprint, j) => (
          <BlueprintToken
            key={j}
            coord={{
              row: (TOKEN_SIZE + PADDING) * i,
              col: (TOKEN_SIZE + PADDING) * j,
            }}
            blueprint={blueprint}
          />
        ))
    )}
  </svg>
);
