import {
  Coordinates,
  TerrainMap,
  clusterSwarm,
  containsCoords,
  coordinatesOverlap,
  getAdjacentToSelection,
  isAdjacentTo,
  isWithinMap,
  sameCoordinates,
  startCorners,
  subtractCoordinates,
} from "../Map";
import { Terrain, getBoundingBox } from "../Map/Terrain";
import { Player } from "../Player";

export enum Blueprint {
  O = "O",
  I = "I",
  T = "T",
  S = "S",
  Z = "Z",
  L = "L",
  J = "J",
}

export const BlueprintList: Blueprint[] = [
  Blueprint.O,
  Blueprint.I,
  Blueprint.T,
  Blueprint.S,
  Blueprint.L,
];

const flipBlueprint: (blueprint: Blueprint) => Blueprint[] = (blueprint) =>
  ({
    [Blueprint.O]: [Blueprint.O],
    [Blueprint.I]: [Blueprint.I],
    [Blueprint.T]: [Blueprint.T],
    [Blueprint.S]: [Blueprint.S, Blueprint.Z],
    [Blueprint.Z]: [Blueprint.Z, Blueprint.S],
    [Blueprint.L]: [Blueprint.L, Blueprint.J],
    [Blueprint.J]: [Blueprint.J, Blueprint.L],
  }[blueprint]);

const BLUEPRINT_PATTERNS: { [k in Blueprint]: boolean[][][] } = {
  [Blueprint.O]: [
    [
      [true, true],
      [true, true],
    ],
  ],
  [Blueprint.I]: [[[true, true, true, true]], [[true], [true], [true], [true]]],
  [Blueprint.T]: [
    [
      [true, true, true],
      [false, true, false],
    ],
    [
      [false, true],
      [true, true],
      [false, true],
    ],
    [
      [false, true, false],
      [true, true, true],
    ],
    [
      [true, false],
      [true, true],
      [true, false],
    ],
  ],
  [Blueprint.S]: [
    [
      [true, true, false],
      [false, true, true],
    ],
    [
      [false, true],
      [true, true],
      [true, false],
    ],
  ],
  [Blueprint.Z]: [
    [
      [true, false],
      [true, true],
      [false, true],
    ],
    [
      [false, true, true],
      [true, true, false],
    ],
  ],
  [Blueprint.L]: [
    [
      [true, true, true],
      [true, false, false],
    ],
    [
      [true, true],
      [false, true],
      [false, true],
    ],
    [
      [false, false, true],
      [true, true, true],
    ],
    [
      [true, false],
      [true, false],
      [true, true],
    ],
  ],
  [Blueprint.J]: [
    [
      [true, true],
      [true, false],
      [true, false],
    ],
    [
      [true, true, true],
      [false, false, true],
    ],
    [
      [false, true],
      [false, true],
      [true, true],
    ],
    [
      [true, false, false],
      [true, true, true],
    ],
  ],
};

const getBlueprintCoordinates: (
  blueprint: Blueprint,
  coord: Coordinates,
  fr: number
) => Coordinates[] = (blueprint, coord, rotation) =>
  flipBlueprint(blueprint)
    .flatMap((bp) => BLUEPRINT_PATTERNS[bp])
    [rotation].map((row, i) =>
      row.map((col, j) =>
        col
          ? {
              row: coord.row + i,
              col: coord.col + j,
            }
          : null
      )
    )
    .flatMap((row) => row.filter((col) => col !== null) as Coordinates[]);

export const getAllBlueprintCoordinatesWithTranslation: (
  blueprint: Blueprint,
  coord: Coordinates
) => Coordinates[][] = (blueprint, coord) =>
  flipBlueprint(blueprint)
    .flatMap((bp) => BLUEPRINT_PATTERNS[bp])
    .map((rotation, irotation) =>
      rotation
        .flatMap((row, i) =>
          row.map((_col, j) => ({ row: coord.row - i, col: coord.col - j }))
        )
        .map((c) => getBlueprintCoordinates(blueprint, c, irotation))
        .filter((bp) => bp.every(isWithinMap))
    )
    .flat();

const samePattern: (
  pattern1: Coordinates[],
  pattern2: Coordinates[]
) => boolean = (pattern1, pattern2) =>
  pattern1.every((c1) => pattern2.some((c2) => sameCoordinates(c1, c2)));

const getPatternsMatchingSelection: (
  blueprint: Blueprint,
  selection: Coordinates[]
) => Coordinates[][] = (blueprint, selection) =>
  getAllBlueprintCoordinatesWithTranslation(blueprint, selection[0]).filter(
    (pattern) => samePattern(selection, pattern)
  );

export const validateSpreadingBlueprint: (
  map: TerrainMap,
  pattern: Coordinates[],
  blueprint: Blueprint,
  player: Player
) => boolean = (map, pattern, blueprint, player) => {
  return getPatternsMatchingSelection(blueprint, pattern).some(
    (selection) =>
      selection.every(isWithinMap) &&
      isFreeTerrainOnly(selection, map) &&
      (isSelectionAdjacentToCoordinates(selection, player.swarm) ||
        coordinatesOverlap(selection, startCorners()))
  );
};

export const validateBuildBlueprint: (
  map: TerrainMap,
  pattern: Coordinates[],
  blueprint: Blueprint,
  player: Player
) => boolean = (map, pattern, blueprint, player) =>
  getPatternsMatchingSelection(blueprint, pattern).some(
    (selection) =>
      containsCoords(player.swarm, selection) &&
      getAdjacentToSelection(selection).every((c) => occupiedByCritters(c, map))
  );

export const validateManufactureBlueprint: (
  map: TerrainMap,
  pattern: Coordinates[],
  blueprint: Blueprint,
  player: Player
) => boolean = (map, pattern, blueprint, player) => {
  const swarm = clusterSwarm(player.swarm).find((s) =>
    s.some((c) => sameCoordinates(c, pattern[0]))
  );
  if (
    swarm !== undefined &&
    getAdjacentToSelection(swarm).some((c) => isBuilding(c, map, blueprint))
  ) {
    return getPatternsMatchingSelection(blueprint, pattern).some((selection) =>
      containsCoords(player.swarm, selection)
    );
  }
  return false;
};

const coordsAreAligned = (coord1: Coordinates, coord2: Coordinates) => {
  const { row, col } = subtractCoordinates(coord1, coord2);
  return (row === 0) !== (col === 0);
};

const patternsAreAligned = (
  pattern1: Coordinates[],
  pattern2: Coordinates[]
) => {
  const bb1 = getBoundingBox(pattern1);
  const bb2 = getBoundingBox(pattern2);
  return (
    coordsAreAligned(bb1.topLeft, bb2.topLeft) &&
    coordsAreAligned(bb1.bottomRight, bb2.bottomRight)
  );
};

export const validateDeliveryBlueprint: (
  map: TerrainMap,
  blueprint: Blueprint,
  location: Coordinates[],
  selection: Coordinates[],
  id: number
) => boolean = (map, blueprint, location, selection, id) =>
  getPatternsMatchingSelection(blueprint, selection).some(
    (selection) =>
      selection.every(isWithinMap) &&
      isFreeTerrainOnly(selection, map) &&
      patternsAreAligned(location, selection) &&
      isAdjacentOpponentOwnedTerrains(selection, id, map)
  );

const isAdjacentOpponentOwnedTerrains = (
  selection: Coordinates[],
  id: number,
  map: TerrainMap
) =>
  getAdjacentToSelection(selection)
    .map((c) => map[c.row][c.col])
    .some((t) => t.state === "occupied" && t.owner !== id);

const isSelectionAdjacentToCoordinates = (
  selection: Coordinates[],
  swarm: Coordinates[]
) => selection.some((c1) => swarm.some((c2) => isAdjacentTo(c1, c2)));

const isFreeTerrainOnly = (selection: Coordinates[], map: TerrainMap) =>
  selection.every((c) => map[c.row][c.col].state === "free");

const occupiedByCritters = (coord: Coordinates, map: TerrainMap) =>
  ((terrain: Terrain) => terrain.state === "occupied")(
    map[coord.row][coord.col]
  );

const isBuilding = (coord: Coordinates, map: TerrainMap, token: Blueprint) =>
  ((terrain: Terrain) =>
    terrain.state === "built" && terrain.blueprint === token)(
    map[coord.row][coord.col]
  );
