import * as Swarms from "@hervan/swarms";
import { cellSize, fontSize } from "../theme";
import { Terrain, colorFromTerrain } from "./Terrain";

type Action = Swarms.Action.Action;
type Selection = Swarms.Action.Selection<Action>;
type Coordinates = Swarms.Map.Coordinates;
type TerrainMap = Swarms.Map.TerrainMap;

const { containsCoord } = Swarms.Map;

const resolveTerrain = (
  selection: Selection,
  coord: Coordinates,
  deselectLocation: (...args: any[]) => void,
  deselectDestination: (...args: any[]) => void,
  selectLocation: (...args: any[]) => void,
  selectDestination: (...args: any[]) => void
) => {
  if (selection.slot === undefined) return;
  if (containsCoord(selection.location, coord)) {
    deselectLocation(coord);
  } else if (containsCoord(selection.destination, coord)) {
    deselectDestination(coord);
  } else if (selection.location.length < 4) {
    selectLocation([coord], selection.slot.action);
  } else {
    selectDestination([coord]);
  }
};

const terrainBorder = 0.2;
const terrainStyle: React.CSSProperties = {
  border: `${terrainBorder}vmin solid white`,
  margin: "0vmin",
  padding: "0vmin",
  width: `${cellSize - 2 * terrainBorder}vmin`,
  height: `${cellSize - 2 * terrainBorder}vmin`,
  textAlign: "center",
  cursor: "pointer",
};

interface MapProps {
  map: TerrainMap;
  selection: Selection;
  deselectLocation: (...args: any[]) => void;
  deselectDestination: (...args: any[]) => void;
  selectLocation: (...args: any[]) => void;
  selectDestination: (...args: any[]) => void;
}

export const Map = ({
  map,
  selection,
  deselectLocation,
  deselectDestination,
  selectLocation,
  selectDestination,
}: MapProps) => (
  <table
    style={{
      borderSpacing: "0vmin",
      emptyCells: "show",
    }}
  >
    <tbody>
      {map.map((row, i) => (
        <tr key={i}>
          {row.map((terrain, j) => (
            <td
              key={j}
              style={{
                ...terrainStyle,
                fontSize: `${fontSize}vmin`,
                backgroundColor: colorFromTerrain(
                  terrain.state === "built" ? undefined : terrain.type
                ),
              }}
              onClick={() =>
                resolveTerrain(
                  selection,
                  { row: i, col: j },
                  deselectLocation,
                  deselectDestination,
                  selectLocation,
                  selectDestination
                )
              }
            >
              <Terrain terrain={terrain} selection={selection} />
            </td>
          ))}
        </tr>
      ))}
    </tbody>
  </table>
);
