import { deepAssign, numberlist } from "@hervan/utils";
import { Player } from "Player";
import { NUM_PLAYERS, SwarmsState } from "../..";
import { Action, ActionList } from "../../Action";
import { Blueprint } from "../../Blueprint";
import { Slot } from "./Slot";

type GridRow = {
  [k in Action]: Slot<Action>;
};

export const GRID_SIZE = 4;

const emptyGridRow: (rowNumber: number) => GridRow = (rowNumber) =>
  ActionList.reduce(
    (row: GridRow, action) => ({
      ...row,
      [action]: {
        action,
        row: rowNumber,
        blueprint: undefined,
      } as Slot<typeof action>,
    }),
    {} as GridRow
  );

export type TechGrid = GridRow[];

export const playerStartingGrid: () => TechGrid = () =>
  numberlist(GRID_SIZE).map((i) => emptyGridRow(i));

const isInitialPosition = (grid: TechGrid, row: number, action: Action) =>
  row === 0 &&
  action === ActionList[0] &&
  grid[row][action].blueprint === undefined;

const isAdjacentToUpgradedAction = (grid: TechGrid, row: number, col: number) =>
  [
    ...(row === 0 ? [] : [grid[row - 1][ActionList[col]]]),
    ...(row === GRID_SIZE - 1 ? [] : [grid[row + 1][ActionList[col]]]),
    ...(col === 0 ? [] : [grid[row][ActionList[col - 1]]]),
    ...(col === GRID_SIZE - 1 ? [] : [grid[row][ActionList[col + 1]]]),
  ].some((s) => s.blueprint !== undefined);

export const isUniqueBlueprint = (
  grid: TechGrid,
  row: number,
  action: Action,
  blueprint: Blueprint
) =>
  grid[row][action].blueprint === blueprint ||
  (grid[row][action].blueprint === undefined &&
    grid.every(
      (row) =>
        row[action].blueprint === undefined ||
        row[action].blueprint !== blueprint
    ) &&
    ActionList.every(
      (a) =>
        grid[row][a].blueprint === undefined ||
        grid[row][a].blueprint !== blueprint
    ));

export const blueprintMastery: (
  grid: TechGrid,
  blueprint?: Blueprint
) => number = (grid, blueprint) =>
  blueprint === undefined
    ? 0
    : grid
        .flatMap((row) => ActionList.map((action) => row[action]))
        .filter(
          (action) =>
            action.blueprint !== undefined && action.blueprint === blueprint
        ).length;

export const isValidActionSelection = (
  player: Player,
  slot: Slot<Action>,
  tokens: Blueprint[]
) => {
  const col = ActionList.findIndex((a) => a === slot.action);

  const previouslyOccupiedSpace =
    slot.blueprint !== undefined && slot.blueprint === tokens[0];

  const uniquenessRequirement = isUniqueBlueprint(
    player.grid,
    slot.row,
    slot.action,
    tokens[0]
  );
  const adjacencyRequirement = isAdjacentToUpgradedAction(
    player.grid,
    slot.row,
    col
  );
  const properNewlyOccupiedSpace =
    slot.blueprint === undefined &&
    uniquenessRequirement &&
    adjacencyRequirement;

  const validSlotPosition = previouslyOccupiedSpace || properNewlyOccupiedSpace;

  const spreadRequirements =
    slot.action === Action.Spread && player.resources.length >= 4;
  const buildRequirements =
    slot.action === Action.Build && player.resources.length >= 4;
  const tradeRequirements = slot.action === Action.Trade;
  const researchRequirements =
    slot.action === Action.Research &&
    slot.blueprint === undefined &&
    tokens.length >= NUM_PLAYERS;

  const validExecutableAction =
    spreadRequirements ||
    buildRequirements ||
    tradeRequirements ||
    researchRequirements;

  return (
    isInitialPosition(player.grid, slot.row, slot.action) ||
    (validSlotPosition && validExecutableAction)
  );
};

export const upgradeGrid = (
  player: Player,
  slot: Slot<Action>,
  tokens: Blueprint[]
): TechGrid =>
  player.grid.map((originalRow, i) =>
    ActionList.reduce(
      (newRow, action) => ({
        ...newRow,
        [action]:
          i === slot.row && action === slot.action
            ? {
                ...originalRow[action],
                blueprint: tokens[0],
              }
            : originalRow[action],
      }),
      originalRow
    )
  );

export const upgradeGameGrid: (G: SwarmsState, id: number) => SwarmsState = (
  G,
  id
) => {
  const currentPlayer = G.players[id];
  const stall = currentPlayer.selection.stall;
  if (stall === undefined) {
    return G;
  }
  const token = G.market.stalls[stall];
  const slot = currentPlayer.selection.slot;
  if (slot === undefined || token === undefined) {
    return G;
  }
  const stalls = G.market.stalls.filter(
    (_, i) => i !== currentPlayer.selection.stall
  );
  const bag = [...G.market.bag, token];
  const market = { ...G.market, stalls, bag };
  const grid = upgradeGrid(
    currentPlayer,
    slot,
    G.market.stalls.filter((t) => t === token)
  );
  const selection = {
    ...currentPlayer.selection,
    slot: grid[slot.row][slot.action],
  };
  const player = { ...currentPlayer, grid, selection };
  return deepAssign({ ...G, market }, player, "players", (p) => p.id === id);
};

export * as Slot from "./Slot";
