export type Track = number[][];

export const moveOnTurnTrack: (
  turnTrack: Track,
  playerPosition: number,
  timeSpent: number
) => Track = (turnTrack, playerPosition, timeSpent) => {
  if (playerPosition === timeSpent) {
    return turnTrack;
  }
  const [id, ...currentTimePosition] = turnTrack[playerPosition];
  if (id === undefined) {
    return turnTrack;
  }
  const nextTimePosition = turnTrack[timeSpent] || [];
  let updatedTurnTrack = [...turnTrack];
  updatedTurnTrack[timeSpent] = [id, ...nextTimePosition];
  updatedTurnTrack[playerPosition] = currentTimePosition;
  return updatedTurnTrack;
};
