import * as Swarms from "@hervan/swarms";
import { emojiFromSpecies } from "Board/Species";
import { COIN_EMOJI, TIME_EMOJI } from "Board/theme";
import { colorFromTerrain, emojiFromBuilding } from "../../Map/Terrain";
import { Slot } from "./Slot";

const { TerrainList } = Swarms.Map.Terrain;
const { SpeciesList } = Swarms.Player.Species;

type Player = Swarms.Player.Player;
type Species = Swarms.Player.Species.Species;
type TerrainMap = Swarms.Map.TerrainMap;
type Action = Swarms.Action.Action;

const { Action, ActionList } = Swarms.Action;

interface GridProps {
  map: TerrainMap;
  player: Player;
  selectedStall?: Swarms.Blueprint.Blueprint;
  selectSlot: (...args: any[]) => void;
}

const itemize = (ds: any, k?: number) =>
  Array.isArray(ds) ? (
    <ul key={k || 0} style={{ paddingInlineStart: "10px" }}>
      {Array.isArray(ds[1]) ? (
        <>
          <li key={k || 0}>{ds[0]}</li>
          {ds.slice(1).map((d, i) => itemize(d, i + 1))}
        </>
      ) : (
        ds.map((d, i) => itemize(d, i))
      )}
    </ul>
  ) : (
    <li key={k || 0}>{ds}</li>
  );

const actionDescriptions = (species: Species) => [
  [
    [
      `${emojiFromSpecies(species).repeat(4)}`,
      `spreading ${emojiFromSpecies(
        species
      )} must be adjacent to existing ${emojiFromSpecies(species)}`,
    ],
  ],
  [
    [
      `${emojiFromBuilding().repeat(4)}`,
      `drawn over ${emojiFromSpecies(species).repeat(4)}`,
      `built ${emojiFromBuilding().repeat(
        4
      )} must be adjacent to critters on all sides`,
    ],
  ],
  [
    [
      `${emojiFromSpecies(species).repeat(4)}`,
      `reward: ${COIN_EMOJI}  price  amount`,
      `additional matching tokens are used as delivered products`,
      [
        `price: blueprint knowledge  delivered tokens`,
        `amount: ${SpeciesList.filter((s) => s !== species)
          .map((s) => emojiFromSpecies(s))
          .join("")} touching ${emojiFromSpecies(species).repeat(4)}`,
      ],
      `origin in terrain matching action, destination perpendicular to origin`,
      `${emojiFromSpecies(species).repeat(
        4
      )} must be adjacent to ${SpeciesList.filter((s) => s !== species)
        .map((s) => emojiFromSpecies(s))
        .join("")}`,
      `give delivered tokens to critter species at will`,
    ],
  ],
  [
    [
      `enable row scoring if occupied at the end of the game`,
      `give 1 token from hand to each opponent to place in same row of research column`,
    ],
  ],
];

const generalConcepts = [
  `market`,
  [
    `if empty, draw 1 token from bag per player tied last on turn track`,
    `token draft: +${TIME_EMOJI}`,
    `current player is the one on top of stack most behind on turn track`,
  ],
  `actions`,
  [
    `use token from hand on action space; return to bag if space had a token`,
    `unused matching tokens from hand return to bag`,
    `time: ${TIME_EMOJI}  blueprint knowledge`,
    `reward: ${COIN_EMOJI}  blueprint knowledge (except trade)`,
    `action locations match blueprint in token`,
  ],
  `tech grid (action spaces)`,
  [
    `token placed in empty or matching space`,
    `token must be unique in both row and column`,
    `space must be adjacent to occupied space`,
    `leave token in used space if it was empty`,
  ],
];

export const Grid = ({ map, player, selectedStall, selectSlot }: GridProps) => (
  <>
    {player.grid.map((row, i) => (
      <tr key={`gridrow${i}`}>
        {ActionList.map((action, col) => (
          <td
            key={`${i}x${col}`}
            style={{
              border: "0.1vmin solid #555",
              backgroundColor:
                action === Action.Trade
                  ? colorFromTerrain(
                      TerrainList[(i + player.id) % ActionList.length]
                    )
                  : "",
              cursor: "pointer",
              height: "20vmin",
              verticalAlign: "top",
              fontSize: "1.4vmin",
            }}
            onClick={() => selectSlot(i, action)}
          >
            <Slot
              blueprint={
                i === player.selection.slot?.row &&
                action === player.selection.slot.action
                  ? player.selection.stall === undefined
                    ? row[action].blueprint
                    : selectedStall
                  : row[action].blueprint
              }
              action={action}
              row={i}
              player={player}
              map={map}
              selected={
                i === player.selection.slot?.row &&
                action === player.selection.slot.action
              }
            />
          </td>
        ))}
        {i === 0 ? (
          <td
            style={{
              border: "0.1vmin solid #555",
              padding: "1vmin",
              verticalAlign: "top",
              fontSize: "1.4vmin",
              width: "20vmin",
            }}
            rowSpan={5}
          >
            <span style={{ fontWeight: "bold" }}>general game concepts</span>
            {itemize(generalConcepts)}
          </td>
        ) : undefined}
      </tr>
    ))}
    <tr>
      {actionDescriptions(player.species).map((d, i) => (
        <td
          key={i}
          style={{
            border: "0.1vmin solid #555",
            fontSize: "12px",
            overflow: "visible",
            overflowWrap: "break-word",
            wordWrap: "break-word",
            verticalAlign: "top",
          }}
        >
          {itemize(d)}
        </td>
      ))}
    </tr>
  </>
);
