import type * as Swarms from "@hervan/swarms";

import { BlueprintToken } from "../Blueprint";
type Blueprint = Swarms.Blueprint.Blueprint;

interface MarketStallProps {
  token: Blueprint;
  selected?: boolean;
}

export const Token = ({ token, selected }: MarketStallProps) =>
  token !== undefined ? (
    <svg
      height="6vmin"
      width="6vmin"
      preserveAspectRatio="xMinYMin slice"
      viewBox="0 0 10 10"
    >
      <BlueprintToken
        coord={{ row: 0, col: 0 }}
        blueprint={token}
        selected={selected}
      />
    </svg>
  ) : null;
