import * as Swarms from "@hervan/swarms";
import { emojiFromBuilding } from "Board/Map/Terrain";
import { emojiFromSpecies } from "Board/Species";
import { Token } from "Board/Token";

type TerrainMap = Swarms.Map.TerrainMap;

type SlotProps = {
  blueprint: Swarms.Blueprint.Blueprint;
  action: Swarms.Action.Action;
  row: number;
  player: Swarms.Player.Player;
  map: TerrainMap;
  selected: boolean;
};

const { scienceScore } = Swarms.Player.Grid.Slot;
const { SpeciesList } = Swarms.Player.Species;

const researchDescriptions = (species: Swarms.Player.Species.Species) => [
  `${emojiFromSpecies(species).repeat(4)} adjacent to ${SpeciesList.filter(
    (s) => s !== species
  )
    .map((s) => emojiFromSpecies(s))
    .join("")}`,
  `${emojiFromBuilding().repeat(
    4
  )} in the largest group of neighbouring ${emojiFromBuilding()}`,
  `customers`,
  `completed grid rows and columns`,
];

export const Slot = ({
  blueprint,
  action,
  row,
  player,
  map,
  selected,
}: SlotProps) => (
  <div
    style={{
      position: "relative",
      margin: "1vmin",
    }}
  >
    <div
      style={{
        position: "absolute",
        top: `12vmin`,
        left: `12vmin`,
      }}
    >
      <Token token={blueprint} selected={selected} />
    </div>
    <div style={{ position: "relative", top: `0vmin`, left: `0vmin` }}>
      {action === Swarms.Action.Action.Research
        ? `${researchDescriptions(player.species)[row]}: ${
            scienceScore(player, map)[row]
          }`
        : undefined}
    </div>
  </div>
);
