import * as Swarms from "@hervan/swarms";
import { COIN_EMOJI, TIME_EMOJI, fontSize } from "../theme";
import { Grid } from "./Grid";

type TerrainType = Swarms.Map.Terrain.TerrainType;
type TerrainMap = Swarms.Map.TerrainMap;
type PlayerState = Swarms.Player.Player;

const { ActionList } = Swarms.Action;
const { TerrainType } = Swarms.Map.Terrain;

interface PlayerProps {
  map: TerrainMap;
  player: PlayerState;
  selectedStall?: Swarms.Blueprint.Blueprint;
  selectSlot: (...args: any[]) => void;
  winner: boolean;
  isActive: boolean;
  passTurn: (...args: any[]) => void;
  discardSelection: (...args: any[]) => void;
}

const resourceEmojiFromTerrain = (t: TerrainType) => {
  switch (t) {
    case TerrainType.Water:
      return "";
    case TerrainType.Desert:
      return "";
    case TerrainType.Forest:
      return "";
    case TerrainType.Mountain:
      return "";
    default:
      return "";
  }
};

export const Player = ({
  map,
  player,
  selectedStall,
  selectSlot,
  winner,
  isActive,
  passTurn,
  discardSelection,
}: PlayerProps) => (
  <table
    key={`player${player.id}`}
    style={{
      fontSize: `${fontSize}vmin`,
    }}
  >
    <tbody>
      <tr>
        <td colSpan={4} rowSpan={2}>
          {player.resources.map(resourceEmojiFromTerrain)}
        </td>
        <td colSpan={1} style={{ textAlign: "right" }}>
          {isActive ? (
            <span style={{ cursor: "pointer" }} onClick={passTurn}>
              harvest {TIME_EMOJI}
            </span>
          ) : (
            <span style={{ float: "right" }}>wait for your turn</span>
          )}
        </td>
      </tr>
      <tr>
        <td colSpan={1} style={{ textAlign: "right" }}>
          {winner ? "" : null}
          {player.score}{COIN_EMOJI}&nbsp;&nbsp;&nbsp;
          {player.timeSpent}{TIME_EMOJI}
        </td>
      </tr>
      <tr>
        {ActionList.map((action, i) => (
          <td key={i} style={{ textAlign: "right", width: "20vmin" }}>
            {action}
          </td>
        ))}
        <td style={{ textAlign: "right", width: "20vmin" }}>
          {isActive ? (
            <span style={{ cursor: "pointer" }} onClick={discardSelection}>
               discard action
            </span>
          ) : (
            <span style={{ float: "right" }}>wait for your turn</span>
          )}
        </td>
      </tr>
      <Grid
        map={map}
        player={player}
        selectedStall={selectedStall}
        selectSlot={selectSlot}
      />
    </tbody>
  </table>
);
