import { numberlist } from "@hervan/utils";
import { Blueprint, BlueprintList } from "../Blueprint";
import { Shuffle } from "../common";

export type Market = {
  stalls: Blueprint[];
  bag: Blueprint[];
};

export const newMarket: Market = {
  stalls: [],
  bag: [...BlueprintList],
};

const drawOneTokenFromBag = (market: Market, shuffle: Shuffle) => {
  if (market.bag.length === 0) {
    return market;
  }
  const [token, ...bag] = shuffle(market.bag);
  return {
    ...market,
    stalls: [...market.stalls, token],
    bag,
  };
};

export const refillMarketTokens: (
  market: Market,
  shuffle: Shuffle,
  amount: number
) => Market = (market, shuffle, amount) =>
  numberlist(amount).reduce(
    (market) => drawOneTokenFromBag(market, shuffle),
    market
  );
