import * as Swarms from "@hervan/swarms";
import { Fragment } from "react";
import { Token } from "../Token";
import { fontSize } from "../theme";

type MarketState = Swarms.Market.Market;

const { BlueprintList } = Swarms.Blueprint;

interface MarketProps {
  market: MarketState;
  selectedStall?: number;
  draftToken: (...args: any[]) => void;
}

export const Market = ({ market, selectedStall, draftToken }: MarketProps) => (
  <table
    style={{
      fontSize: `${fontSize}vmin`,
    }}
  >
    <thead>
      <tr>
        <th colSpan={2} />
        <th align="left" colSpan={4}>
          market
        </th>
      </tr>
    </thead>
    <tbody>
      <tr>
        <td />
        <td
          style={{
            verticalAlign: "middle",
            textAlign: "right",
          }}
        >
          stalls:
        </td>
        <td colSpan={4}>
          {market.stalls.map((token, i) => (
            <span
              key={i}
              style={{ cursor: "pointer" }}
              onClick={() => draftToken(i)}
            >
              <Token token={token} selected={selectedStall === i} />
            </span>
          ))}
        </td>
      </tr>
      <tr>
        <td />
        <td
          style={{
            verticalAlign: "middle",
            textAlign: "right",
          }}
        >
          bag:
        </td>
        <td colSpan={4}>
          {market.bag.length > 10
            ? BlueprintList.map((token, i) => (
                <Fragment key={i}>
                  <Token token={token} />
                  {market.bag.filter((t) => t === token).length}
                  &nbsp;&nbsp;&nbsp;
                </Fragment>
              ))
            : market.bag.map((token, i) => <Token key={i} token={token} />)}
        </td>
      </tr>
    </tbody>
  </table>
);
