import * as Swarms from "@hervan/swarms";
import { emojiFromSpecies } from "../../Species";

type TerrainType = Swarms.Map.Terrain.TerrainType;
type TerrainState = Swarms.Map.Terrain.Terrain;
type Action = Swarms.Action.Action;
type Selection = Swarms.Action.Selection<Action>;
type Blueprint = Swarms.Blueprint.Blueprint;

const { TerrainType } = Swarms.Map.Terrain;
const { Blueprint } = Swarms.Blueprint;
const { sameCoordinates } = Swarms.Map;
const { SpeciesList } = Swarms.Player.Species;

export const colorFromTerrain = (type: TerrainType | undefined) => {
  switch (type) {
    case TerrainType.Water:
      return "lightblue";
    case TerrainType.Forest:
      return "lightgreen";
    case TerrainType.Desert:
      return "khaki";
    case TerrainType.Mountain:
      return "lightcoral";
    default:
      return "white";
  }
};

export const emojiFromBuilding = (blueprint?: Blueprint) => {
  switch (blueprint) {
    case Blueprint.S:
      return "";
    case Blueprint.O:
      return "";
    case Blueprint.T:
      return "";
    case Blueprint.I:
      return "";
    case Blueprint.L:
      return "";
    default:
      return "";
  }
};

interface TerrainProps {
  terrain: TerrainState;
  selection: Selection;
}

export const Terrain = ({ terrain, selection }: TerrainProps) => {
  const blueprint = selection.location.some((coord) =>
    sameCoordinates(coord, terrain.coord)
  );
  const destination = selection.destination.some((coord) =>
    sameCoordinates(coord, terrain.coord)
  );
  const content =
    terrain.state === "built"
      ? emojiFromBuilding(terrain.blueprint)
      : terrain.state === "occupied"
      ? emojiFromSpecies(SpeciesList[terrain.owner])
      : "";
  return (
    <>{destination ? `<${content}>` : blueprint ? `[${content}]` : content}</>
  );
};
